body {
  font-family: Kanit, sans-serif;
}

.container {
  padding: 45px 100px;
}

.title {
  text-align: center;
  color: #0080ff;
  margin: 0 0 45px;
}

.gamebox {
  width: 1065px;
  height: 790px;
  flex-direction: column;
  gap: 5px;
  margin: 0 auto;
  display: flex;
}

.line {
  flex-direction: row;
  gap: 5px;
  display: flex;
}

.flip-card {
  width: 260px;
  height: 260px;
  perspective: 1000px;
  background-color: rgba(0, 0, 0, 0);
}

.flip-card-inner {
  width: 100%;
  height: 100%;
  text-align: center;
  transform-style: preserve-3d;
  transition: transform .8s;
  position: relative;
}

.flip-card-front, .flip-card-back {
  width: 100%;
  height: 100%;
  cursor: pointer;
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
  border: 1px solid #000;
  border-radius: 6px;
  position: absolute;
}

.flip-card-front {
  background-color: #fff;
}

.flip-card-back {
  background-color: #cdefff;
  background-position: center;
  background-repeat: no-repeat;
  background-size: 180px 180px;
  justify-content: center;
  align-items: center;
  display: flex;
  transform: rotateY(180deg);
}

.volume {
  width: 40px;
  height: 40px;
  fill: #0080ff;
  cursor: pointer;
  background-color: #cdefff;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  transition: all .5s ease-out;
  display: flex;
  position: fixed;
  bottom: 50px;
  right: 50px;
}

.volume:hover, .volume:focus {
  fill: #005cb8;
  transform: scale(1.2);
}

.footer {
  text-align: center;
}

.copyright {
  margin: 45px 0 0;
  display: inline-block;
}

.name {
  color: #005cb8;
}

